import * as React from 'react';
import Calendar from "./calendar";
import {useEffect, useState} from "react";
/**
 * schedule multiple dragging sample
 */
function App() {
    const [user, setUser] = useState([]);
    const [events, setEvents] = useState([]);

    const createDataCalendar = (events) => {

            const fullData = events.map(event => {
                return {
                    Id: event.id,
                    Subject: event.eventDescription,
                    StartTime: event.eventStartDateTime,
                    EndTime: event.eventEndDateTime,
                    TaskId: event.doctorId
                }
            })

        setEvents(fullData)
    }

    useEffect(()=> {
        try {
            fetch('https://calendarpoc.azurewebsites.net/api/Doctor')
                .then(response => response.json())
                .then(docs => setUser(docs))
        }catch (e) {

        }
    },[])


    useEffect(()=> {
        try {
            fetch('https://calendarpoc.azurewebsites.net/api/Event/getallevents')
                .then(response => response.json())
                .then(even => createDataCalendar(even))
        }catch (e) {

        }
    },[])


    return (<Calendar user={user} events={events}/>);
}
export default App;