import React from 'react';
import {extend} from "@syncfusion/ej2-base";
import {resourceData, timelineResourceData} from "./data/eventData";
import {
    Agenda,
    Day, DragAndDrop,
    Inject, Month, Resize,
    ResourceDirective,
    ResourcesDirective,
    ScheduleComponent,
    ViewDirective,
    ViewsDirective, Week, WorkWeek
} from "@syncfusion/ej2-react-schedule";

function Calendar({user,events}) {
    const data = extend([], resourceData.concat(timelineResourceData), null, true);
    //
    // const ownerData = [
    //     { text: 'Nancy', id: 1, color: '#df5286' },
    //     { text: 'Steven', id: 2, color: '#7fa900' },
    //     { text: 'Robert', id: 3, color: '#ea7a57' },
    // ];


    const prepareSendEvent = (event) => {
        console.log('event', event)

        return {
            "eventStartDateTime": event.StartTime.toISOString(),
            "eventEndDateTime": event.EndTime.toISOString(),
            "eventDescription": event.Subject,
            "status": "Approved",
            "doctorId": event.TaskId,
            "cabinetId": 0,
            "clinicId": 0,
            "patientId": 0,
            "patientName": "string",
            "patientEmail": "string"
        }
    }


    const createEvent = (event)=> {
        if(event.requestType && event.requestType == 'eventCreated') {
            if(event.data && event.data.length) {
                try {
                    fetch('https://calendarpoc.azurewebsites.net/api/Event/createappointment', {
                        method: 'POST',
                        body: JSON.stringify(prepareSendEvent(event.data[0])),
                        headers: {
                            "Content-type": "application/json; charset=UTF-8"
                        }
                    })
                        .then(response => response.json())
                        .then(json => {
                            console.log('response: ' + JSON.stringify(json));
                        })
                }catch (e) {

                }
            }
        }
    }

    
    return (<div className='schedule-control-section'>
        <div className='col-lg-12 control-section'>
            <div className='control-wrapper'>
                <ScheduleComponent width='100%' height='650px'
                                   selectedDate={new Date(2023, 0, 4)}
                                   currentView='Month' allowMultiDrag={true}
                                   allowResizing={false}
                                   showQuickInfo={false}
                                   eventSettings={{dataSource: events}}
                                   group={{ resources: ['Owners'] }}
                                   actionComplete={createEvent}
                >
                    <ResourcesDirective>
                        <ResourceDirective
                            field='TaskId'
                            title='Owners'
                            name='Owners'
                            dataSource={user}
                            textField='name'
                            idField='id'
                            colorField='color'>
                        </ResourceDirective>
                    </ResourcesDirective>
                    <ViewsDirective>
                        <ViewDirective option='Month'/>
                        <ViewDirective option='TimelineMonth'/>
                        <ViewDirective option='Day'/>
                    </ViewsDirective>
                    <Inject services={[Day, Week, WorkWeek, Month, Agenda, Resize, DragAndDrop]} />
                </ScheduleComponent>
            </div>
        </div>
    </div>);
}
export default Calendar;